.foodapp{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: url("./images/food_background.jpg");
    background-size: cover;
}

.foodapp h1{
    color: rgb(255, 250, 245);
    margin-top: 30px;
}
.searchform{
    margin-top: 20px ;
}
.app_input{
    padding: 5px 15px;
    min-width: 200px;
    max-width: 100vw;
    font-size: 16px;
    background-color: rgb(218, 211, 204);
    border-radius: 5px;
    border: none;
}
.sbtn{
    padding: 5px 15px;
    font-size: 16px;
    background-color: #0000ff;
    color: aliceblue;
    border:2px solid #0000ff;
    border-radius: 5px;
    margin-left: 6px;
    cursor: pointer;
}
.sbtn:hover{
    background-color: aliceblue;
    color: #0000ff;
}
.app_recipes{
    display: grid;
    margin-top: 20px;
    grid-template-columns: 230px 230px;
    align-items: baseline;
}
.food_label{
    padding: 5px 15px;
    margin-left: 6px;
    border-radius: 5px;
}
@media(min-width:1024px){
    .app_recipes{
        grid-template-columns: 230px 230px 230px 230px;
    }
}
@media(min-width:768px) and (max-width:1023px){
    .app_recipes{
        grid-template-columns: 230px 230px 230px;
    }
}
@media(min-width:768px) and (max-width:1023px){
    .app_recipes{
        grid-template-columns: 230px 230px 230px;
    }
}
@media (max-width:520px){
    .app_recipes{
        grid-template-columns: 230px;
    }
    .app_input {
        padding: 5px 15px;
        min-width: 100px;
        max-width: 50vw;
        font-size: 16px;
    }
    .food_label{
        margin-top: 15px;
    }
}
